@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap);
html,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font-family:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote::before,blockquote::after,q::before,q::after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{padding:0;border:0;font-size:100%;vertical-align:baseline;line-height:1;margin:0;font-family:'Open Sans', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box}

.main-container{min-height:100vh;display:flex;background-color:#e3e2e1}

.header{display:flex;flex-direction:row;align-items:center;justify-content:space-between;height:55px;width:100%;background-color:#3b5998;color:#fff}.header__hamburger-menu{display:none;cursor:pointer;border:1px solid #fff;border-radius:2px;margin-left:1rem}.header__languages{list-style:none;display:inline-flex;flex-direction:row;justify-items:center;align-items:center;font-size:20px;margin:0 5px}.header__icons{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.header__icon{list-style:none;margin:0 5px;text-align:center}.header__profile-image{width:30px;height:30px;border-radius:50%;border:1px solid #fff;background-repeat:no-repeat;background-size:cover;background-position:center}@media screen and (max-width: 980px){.header__hamburger-menu{display:inline-block}.logo-image{display:none}}

.sidebar-icon{display:flex;flex-direction:row;align-items:center;padding:0.5rem 1rem;font-size:14px;font-weight:300;color:#212529}.sidebar-icon:hover{background-color:#cccdcf;cursor:pointer}.text{margin-left:1rem}

.sidebar{width:100%}.sidebar-menu{max-height:70vh;overflow-y:scroll;color:#212529;text-align:start}.responsive-sidebar{display:none;position:absolute;left:0;top:50px;z-index:100;box-shadow:3px 2px 6px 0 #212121;-webkit-box-shadow:3px 2px 6px 0 #212121;-moz-box-shadow:3px 2px 6px 0 #212121;background-color:#e3e2e1}.display{display:block}.aside{display:block}@media screen and (max-width: 980px){.aside{display:none}}

.footer{height:30vh;text-align:center}.footer div{margin:auto}.footer__links{display:inline-block;margin:0.5rem;font-size:1rem;line-height:2rem;color:#212529;cursor:pointer}.footer__links:hover{color:black}

.main-content{margin:1rem auto;width:65%}.main-content__tabs{margin-bottom:1rem;display:flex;justify-content:space-around;align-items:center}.main-content__tabs div{padding:0.7rem;width:50%;cursor:pointer}.form-section{width:70%;justify-self:center;background-color:#fff}.active{background-color:#aaa}.form{margin:auto;width:70%}.form-inputs{display:flex;flex-direction:column;justify-self:center}.form-input{width:100%}.progress-bar{display:inline-flex;align-items:center;justify-content:space-between;width:100%;margin-bottom:1rem}.progress-bar span{width:20%;height:10px;border-radius:3px;background-color:#d2d4d2}.progress-barsmall{color:#48494a}.submit-button{width:10rem;padding:0.5rem 1rem;border:1px solid #000;cursor:pointer}.address{display:flex;flex-wrap:wrap;justify-content:space-between}.address div{width:45%}.input{width:100%;border:1px solid black;height:30px;font-size:18px;margin-bottom:1rem}.error-message{color:red;display:inline-block}@media screen and (max-width: 620px){.main-content{width:90%;margin:1rem auto}.submit-button{width:7rem}}

